
@import url('https://fonts.googleapis.com/css?family=Share+Tech+Mono');
@import url('https://fonts.googleapis.com/css?family=Orbitron');



body {
  background-color: black;
  color: rgba( 0, 150, 254, 1 );
  font-size: 16px;
}

.App {
  text-align: center;
}

h1 {
  color: rgba( 0, 150, 254, 1 );
  font-family: 'Orbitron', cursive;
  font-weight: 400;
  font-size: 3.4375em;
}

.modal h1 {
  font-family: "Share Tech Mono", cursive;
  font-size: 3.125em;
  color: rgba( 0, 150, 254, 1 )
}

.board {
  position: relative;
  margin-top: 2.5%;
  width: 250px;
  height: 250px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-shadow: 5px 5px 5px rgba( 0, 150, 254, 1 );
          box-shadow: 5px 5px 5px rgba( 0, 150, 254, 1 );
}

.board > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: black;
  border: 3px solid rgba( 0, 150, 254, 1 );
  width: 33.33%;
  height: 33.33%;
  font-size: 1.5625em;
  font-weight: 600;
}

.flex {
  display: -ms-flexbox;
  display: flex;
}

.center {
  -ms-flex-pack: center;
      justify-content: center;
}

.btn {
  margin: 10px;
  border-radius: 10px;
  border-color: rgba(0, 150, 254, 1);
  background-color: #000;
  color: rgba( 0, 150, 254, 1 );
  font-family:  "Share Tech Mono", cursive;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}

.btn.focused {
  border-color: rgba( 0, 150, 254, 1 );
  -webkit-box-shadow: 0 0 45px 15px rgba( 0, 150, 254, .4 );
          box-shadow: 0 0 45px 15px rgba( 0, 150, 254, .4 );
}

.modal {
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 1000;
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 0.9;
  background-color: black;
}



